"use strict";
boann.controller('flightsController', ['$scope', '$http', '$window', function($scope, $http, $window) {
    $scope.URI           = "../../libraries/callback/callback_msfs.php"; 
    $scope.refresh       = 35000;
    
    fetchFlights();
    setInterval(fetchFlights, $scope.refresh);

    function fetchFlights(){
        $http.get($scope.URI, {params:{action:"getFlights"}}).then(function(data){
            if(data.status === 200){
                $scope.flights = data.data;               
            };
        });
    }
}]);
